import "../../css/Weather.css";
import { useState } from "react";
import { motion } from "framer-motion";
import { pageVariants, pageTransitions } from "../Home";

const Weather = () => {
  const [cityVal, setCityVal] = useState("");

  const [cityName, setCityName] = useState("city");
  const [cityTemp, setCityTemp] = useState(0);

  const [mood, setMood] = useState("");
  const [visible, setVisible] = useState(false);

  const getData = async () => {
    try {
      const res = await fetch(
        `https://api.openweathermap.org/data/2.5/weather?q=${cityVal}&units=metric&appid=aacf52457ee9b815bfb1798b2e142395`
      );
      const data = await res.json();
      setCityName(`${data.name}, ${data.sys.country}`);
      setCityTemp(data.main.temp);
      setMood(data.weather[0].main);
      setVisible(true);
    } catch (error) {
      setVisible(false);
    }
  };

  const Submit = (e) => {
    e.preventDefault();
    getData();
  };

  const Moody = () => {
    if (mood === "Rain") {
      return <i className="fas fa-cloud-rain"></i>;
    } else if (mood === "Clouds") {
      return <i className="fas fa-cloud"></i>;
    } else {
      return <i className="fas fa-sun"></i>;
    }
  };

  const getWeekDay = () => {
    var weekday = [];
    weekday[0] = "Sunday";
    weekday[1] = "Monday";
    weekday[2] = "Tuesday";
    weekday[3] = "Wednesday";
    weekday[4] = "Thursday";
    weekday[5] = "Friday";
    weekday[6] = "Saturday";

    let days = weekday[new Date().getDay()];
    return days;
  };

  return (
    <motion.div
      initial="initial"
      animate="in"
      exit="out"
      transition={pageTransitions}
      variants={pageVariants}
      className="container"
    >
      <div className="row">
        <div className="col-md-10 col-12 mx-auto">
          <form className="temp_form">
            <input
              type="text"
              className="city_field"
              placeholder="Enter Your City Name"
              value={cityVal}
              autoComplete="off"
              onChange={(e) => setCityVal(e.target.value)}
            />
            <button onClick={Submit} id="submitBtn">
              Search
            </button>
          </form>
        </div>
        <div className="tempInformation">
          <div className="top_layer">
            <p id="day">{getWeekDay()}</p>

            <p id="today_data">{`${new Date().getDate()} / ${new Date().getMonth()} / ${new Date().getFullYear()}`}</p>
          </div>
          {visible && (
            <div className="main_layer">
              <p id="city_name">{cityName}</p>
              <div className="middle_layer data_hide">
                <p id="temp">
                  <span>{cityTemp}</span>
                  <sup>o</sup>C
                </p>
                <p id="temp_status">{Moody()}</p>
              </div>
            </div>
          )}
        </div>
      </div>
    </motion.div>
  );
};

export default Weather;
