import { motion } from "framer-motion";
import "../css/About.css";
import { pageTransitions, pageVariants } from "./Home";

const About = () => {
  return (
    <>
      <motion.div
        initial="initial"
        animate="in"
        exit="out"
        variants={pageVariants}
        transition={pageTransitions}
        className="container-fluid About"
      >
        <div className="row">
          <div className="col-md-6 col-12 text-center header_text">
            <h1 className="saad">Saad Khatri</h1>
            <p className="">MERN Stack Developer</p>
          </div>
          <div className="col-md-6 col-12 bg-ligh d-flex justify-content-center image">
            <img src="saad-khatri.jpg" alt="mrsaad" />
          </div>
        </div>
      </motion.div>
    </>
  );
};

export default About;
