import "../../css/MathPlayground.css";
import AgeCalculator from "./AgeCalculator";
import Calculator from "./Calculator";
import { motion } from "framer-motion";
import { pageTransitions, pageVariants } from "../Home";

const MathPlayground = () => {
  return (
    <>
      <motion.div
        className="container"
        initial="initial"
        animate="in"
        exit="out"
        transition={pageTransitions}
        variants={pageVariants}
      >
        <div className="row">
          <div className="col-md-10 col-12 mx-auto mathplayground">
            <Calculator />
            <AgeCalculator />
          </div>
        </div>
      </motion.div>
    </>
  );
};

export default MathPlayground;
